@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  src: local(""), url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
}
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
  src: local(""), url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
}
.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  overflow: hidden;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
.page__body {
  font-family: "PT Sans", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 26px;
  font-style: normal;
  text-transform: uppercase;
  background-color: #eeeeee;
  color: #000000;
  margin: 0;
}
.page__container {
  background-color: #ffffff;
}
.page-main {
  margin-bottom: 19px;
}
@media (min-width: 1200px) {
  .page__container {
    width: 1200px;
    margin: 0 auto;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  }
  .page-main {
    margin-bottom: 44px;
  }
}
.page-header {
  padding-bottom: 56px;
  background: #ffffff;
  position: relative;
}
.page-header__logo {
  position: absolute;
  z-index: 1;
  bottom: -28px;
  left: 50%;
  height: 84px;
  transform: translateX(-50%);
}
@media (min-width: 768px) {
