.photo {
  background-color: @white;
  margin-bottom: 31px;
}

.photo__list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.photo__image {
  position: relative;
  height: 158px;
  background-color: @extrablue;
  background-image: url("../img/catalog-background-desktop@1x.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  text-align: center;
  margin-bottom: 32px;

  @media (min-resolution: 2dppx) {
    background-image: url("../img/catalog-background-desktop@2x.jpg");
  }
}

.photo__image-before::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 57px;
  background-image: url("../img/navigation-detal.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.photo__sedona {
  position: absolute;
  top: 45%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.photo__item {
  margin-bottom: 22px;
  border-bottom: 1px solid @extraspace;
}

.photo__title {
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  text-align: center;
  margin-bottom: 28px;
}

.photo__text {
  line-height: 24px;
  color: @space;
  text-align: center;
  margin: 0;
  padding: 0 20px;
  margin-bottom: 54px;
}

.photo__box {
  padding-bottom: 18px;
}

.photo__intro {
  font-weight: 700;
  font-size: 14px;
  margin: 0;
  line-height: 24px;
}

.photo__description {
  padding: 0 20px;
}

.photo__container {
  display: flex;
  padding: 0 20px;
}

.photo__article {
  margin: 0;
  line-height: 24px;
  font-size: 14px;
  margin-bottom: 14px;
  text-transform: none;
  color: @space;
}

.photo__number {
  margin: 0;
  font-weight: 700;
  line-height: 24px;
}

.photo__img {
  object-fit: cover;
  width: 100%;
  height: 280px;
  padding: 0 20px;
}

.photo__button {
  display: block;
  height: 21px;
  width: 23px;
  top: 0;
  left: 0;
  margin-right: 14px;
  background: none;
  background-image: url("../img/like-icon.svg");
  border: 0;
  cursor: pointer;
}

@media (min-width: @tablet-width) {
  .photo {
    margin-bottom: 57px;
  }

  .photo__list {
    display: flex;
    flex-wrap: wrap;
    padding: 0 60px;
  }

  .photo__sedona {
    width: 457px;
    height: 78px;
    position: absolute;
    top: 46%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  .photo__item {
    margin: 0;
    border-bottom: none;
  }

  .photo__image {
    height: 290px;
    margin-bottom: 62px;
  }

  .photo__img {
    display: block;
    padding: 0;
    height: 267px;
    width: 100%;
  }

  .photo__title {
    margin: 0;
    font-size: 30px;
    margin-bottom: 34px;
  }

  .photo__text {
    margin-bottom: 78px;
    padding: 0 165px;
  }

  .photo__item--small {
    width: 50%;
  }

  .photo__item--big {
    width: 100%;
  }

  .photo__img--first {
    height: 400px;
  }

  .photo__block {
    width: 50%;
  }

  .photo__box {
    background-color: @basicspace;
    display: flex;
    justify-content: space-between;
    padding: 0;
    padding-top: 16px;
    padding-bottom: 16px;
  }

  .photo__article {
    margin-bottom: 0;
  }

  .photo__container {
    display: flex;
    align-items: center;
    padding: 0;
    min-width: 84px;
  }

  .photo__button {
    margin-right: 9px;
  }
}

@media (min-width: @desktop-width) {
  .photo__list {
    padding: 0;
    margin-bottom: 61px;
  }

  .photo__button {
    display: block;
    height: 21px;
    width: 23px;
    top: 0;
    left: 0;
    margin-right: 8px;
    background: none;
    background-image: url("../img/like-icon-white.svg");
    border: 0;
    cursor: pointer;
  }

  .photo__image {
    height: 302px;
    margin-bottom: 99px;
  }

  .photo__text {
    margin-bottom: 79px;
    padding: 0 378px;
  }

  .photo__item {
    position: relative;
    color: @white;
  }

  .photo__article {
    color: @white;
  }

  .photo__item--small {
    width: 33.33%;
  }

  .photo__box {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    background: rgba(@black, 0.3);
  }
}
